<template>
  <div>
    <!-- header begin -->
    <div class="nav-bar noshadow">
      <div class="nav-bar-left" @click="goBack()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-search" @click="goSearch">
        <div class="header-search">
          <search-box ref="searchBox" :search-input="selectGoodsDatas.search_input" />
        </div>
      </div>
      <div class="nav-bar-right" @click="showQhzsBtn()">
        <div :class="showQhzs == false ? 'switch-goodslist active':'switch-goodslist'">
          <i class="iconfont iconliebiao" />
        </div>
      </div>
    </div>
    <!-- header end -->

    <van-sticky>
      <!-- 条件筛选 begin -->
      <div class="sortbar">
        <dl :class="active===0?'active':''" @click="changeClass(0)">
          <dt>配送地址</dt>
        </dl>
        <dl :class="active===1?'active':''" @click="changeClass1(1)">
          <!-- <van-dropdown-menu active-color="#1890FF">
            <van-dropdown-item ref="item" v-model="value1" title="销量" :options="option1" @close="changeClass(1,'sale_count',value1)" />
          </van-dropdown-menu> -->
          <dt>销量</dt>
          <dd v-if="xlSx!==''">
            <div class="sortbar-inline">
              <em v-if="xlSx" class="asc">
                <i class="iconfont iconarrow-up" />
              </em>
              <em v-else class="desc">
                <i class="iconfont iconarrow-down" />
              </em>
            </div>
          </dd>
          <!-- <i :class="sxjxState ? 'iconfont icondaoxu2':'iconfont iconshengxu'" /> -->
        </dl>
        <dl :class="active===2?'active':''" @click="changeClass1(2)">
          <!-- <van-dropdown-menu active-color="#1890FF">
            <van-dropdown-item ref="item" v-model="value2" title="价格" :options="option2" @close="changeClass(2,'price_now',value2)" />
          </van-dropdown-menu> -->
          <dt>价格</dt>
          <dd v-if="jgSx!==''">
            <div class="sortbar-inline">
              <em v-if="jgSx" class="asc">
                <i class="iconfont iconarrow-up" />
              </em>
              <em v-else class="desc">
                <i class="iconfont iconarrow-down" />
              </em>
            </div>
          </dd>
        </dl>
        <dl :class="active===3?'active':''" @click="changeClass1(3)">
          <!-- <van-dropdown-menu active-color="#1890FF">
            <van-dropdown-item ref="item" v-model="value3" title="上架时间" :options="option3" @close="changeClass(3,'update_time',value3)" />
          </van-dropdown-menu> -->
          <dt>上架时间</dt>
          <dd v-if="sjsjSx!==''">
            <div class="sortbar-inline">
              <em v-if="sjsjSx" class="asc">
                <i class="iconfont iconarrow-up" />
              </em>
              <em v-else class="desc">
                <i class="iconfont iconarrow-down" />
              </em>
            </div>
          </dd>
        </dl>
        <dl @click="showPopup">
          <dt>筛选</dt>
          <dd>
            <i class="iconfont iconfilter" />
          </dd>
        </dl>
      </div>
      <!-- 条件筛选 end -->

      <div ref="tab" class="scrolltab-fillet">
        <!-- <base-scroll
          ref="scrollContent"
          :data="itemList"
          :scroll-x="true"
        > -->
        <ul ref="tabWrapper" class="tab_content">
          <li v-for="(nav,index) of navs" v-if="nav.dsbs!=='XJZQ' && nav.dsbs!=='JJZQ'" :key="index" ref="tabitem" class="tab_item" :class="active1===nav.dsbs?'active':''">
            <span class="tit" @click="searchByZq(nav.dsbs,index)">{{ nav.name }}</span>
          </li>
          <!-- <li ref="tabitem" class="tab_item" :class="active1===''?'active':''">
            <span class="tit" @click="searchByZq('',0)">全部</span>
          </li>
          <li ref="tabitem" class="tab_item" :class="active1==='KJXY'?'active':''">
            <span class="tit" @click="searchByZq('KJXY',1)">框架协议</span>
          </li>
          <li ref="tabitem" class="tab_item" :class="active1==='JD'?'active':''">
            <span class="tit" @click="searchByZq('JD',2)">京东专区</span>
          </li>
          <li ref="tabitem" class="tab_item" :class="active1==='SN'?'active':''">
            <span class="tit" @click="searchByZq('SN',3)">苏宁专区</span> -->
          <!-- <van-dropdown-menu>
                <van-dropdown-item ref="item" title="品牌">
                  <div class="dropdown-list">
                    <div class="dropdown-list-c">
                      <ul>
                        <li class="active">
                          <i class="iconfont iconduihao" />
                          品牌名称1
                        </li>
                      </ul>
                    </div>
                    <div class="foot-btn">
                      <ul>
                        <li><van-button type="info" round plain size="large">重置</van-button></li>
                        <li><van-button type="info" round size="large">确定</van-button></li>
                      </ul>
                    </div>
                  </div>
                </van-dropdown-item>
              </van-dropdown-menu> -->
          <!-- </li> -->
        </ul>
        <!-- </base-scroll> -->
      </div>
    </van-sticky>

    <!-- 右侧筛选条件 begin -->
    <van-popup v-model="show" position="right" :style="{ width: '100%' }">
      <div class="selectside">
        <div class="selectside-content">
          <div style="margin-top:40px">
            <div v-if="searchList.categorySearchList&&searchList.categorySearchList.length>0" class="category-list">
              <h2 class="tit">商品分类<i class="iconfont iconguanbi" style="float:right" @click="guanbi" /></h2>
              <ul>
                <li v-for="(item,index) of searchList.categorySearchList" :key="index" :class="{active:index===isActive1}">
                  <div class="name" @click="clickCategory(item,item.id,index)"> {{ item.name }}[{{ item.COUNT }}]</div>
                  <em class="del" @click="chongzhi(1)" />
                </li>
              </ul>
            </div>
            <div class="category-list">
              <h2 class="tit">品牌<i v-if="searchList.categorySearchList&&searchList.categorySearchList.length===0" class="iconfont iconguanbi" style="float:right" @click="guanbi" /></h2>
              <ul>
                <li v-for="(item,index) of searchList.brandList" :key="index" :class="{active:index===isActive2}">
                  <div class="name" @click="clickBrandName(item,item.name,index)"> {{ item.name }}</div>
                  <em class="del" @click="chongzhi(2)"><i class="iconfont iconguanbi" /></em>
                </li>
              </ul>
            </div>
            <div class="category-list">
              <h2 class="tit">价格区间</h2>
              <ul>
                <li v-for="(item,index) of searchList.priceList" :key="index" :class="{active:index===isActive3}">
                  <div class="name" @click="clickPrice(item,item.value,index)"> {{ item.label }}</div>
                  <em class="del" @click="chongzhi(3)"><i class="iconfont iconguanbi" /></em>
                </li>
              </ul>
              <div v-if="state" class="input-price">
                <van-row type="flex" justify="center">
                  <van-col span="11">
                    <van-field v-model="price1" placeholder="输入最低价" @input="price1 = oninput(price1)" />
                  </van-col>
                  <van-col style="text-align:center;" span="2">-</van-col>
                  <van-col span="11">
                    <van-field v-model="price2" placeholder="输入最高价" @input="price2 = oninput(price2)" />
                  </van-col>
                </van-row>
              </div>
            </div>
          </div>
        </div>
        <!-- 底部按钮 begin -->
        <div class="foot-btn fixed">
          <ul>
            <li><van-button type="info" round plain size="large" @click="goReset()">重置</van-button></li>
            <li><van-button type="info" round size="large" @click="goSearch1()">确定</van-button></li>
          </ul>
        </div>
        <!-- 底部按钮 end -->
      </div>
    </van-popup>
    <!-- 右侧筛选条件 end -->
    <!-- <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        :immediate-check="false"
        @load="onLoad"
      > -->
    <common-list
      ref="ggcommonlist"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >
      <div class="container-mini goodslist-container">
        <div class="content">
          <div :class="showQhzs == true ? 'goods-main-horizontal':'goods-main'">
            <!-- 商品循环列表 begin -->
            <div v-for="(item,index) of goodsList" :key="index" class="goods-list" @click="goGoodsdetail(item.product_id,item.name)">
              <div class="goods-img">
                <img v-if="item.imagePath" :src="item.imagePath" :onerror="defaultImg">
                <img v-else src="@/assets/no-img.png">
              </div>
              <div v-if="!stockStatus[index]" class="sold-out" />
              <div class="goods-list-c">
                <div class="goods-price">
                  <span class="num">{{ item.price_now }}</span>元
                  <del>
                    <span class="num">{{ item.price }}</span>元
                  </del>
                </div>
                <div class="goods-name" v-html="item.highlight_name" />
                <div class="goods-description">{{ item.salecontent }}</div>
                <div class="goods-sales"><em>已售{{ item.sale_count?item.sale_count:0 }}</em><em>{{ item.hp_count?item.hp_count:0 }}条好评</em></div>
                <div class="goods-label">
                  <van-tag plain type="primary">{{ item.sourceName }}</van-tag>
                  <div v-if="item.dsbs!=='KJXY'" onClick="event.cancelBubble = true" class="goods-compare" @click="addCompare(item.product_id,item.dsbs)"><i class="iconfont iconduibivs" />{{ checked.indexOf(item.product_id)!==-1?'取消对比':'加入对比' }}</div>
                </div>
              </div>
            </div>
            <!-- 商品循环列表 end -->
          </div>
        </div>
      </div>
    </common-list>
    <!-- 悬浮分页 begin -->
    <!-- <div class="page-fixed">
            <span class="page-number">{{ number }}/{{ totalNumber }}</span>
          </div> -->
    <!-- 悬浮分页 end -->

    <!-- 对比 begin -->
    <div class="aside-fixed aside-fixed-bar">
      <div class="aside-icon" @click="goGoodsCompare()">
        <i class="iconfont iconduibi" />
        <div class="num">{{ checked.length }}</div>
      </div>
    </div>
    <!-- 对比 end -->

    <!-- 已选地址 begin -->
    <div v-if="sheng" class="selected-address">
      <div class="selected-address-list">
        当前选择：<span>{{ sheng }}</span> <span>{{ shi }}</span> <span>{{ xian }}</span> <span>{{ xiang }}</span>
      </div>
      <div v-if="sheng" class="reselect-address" @click="addressShow = true">重新选择</div>
    </div>
    <!-- 已选地址 end -->

    <div class="common-space" />
    <Backtop class="scrollTop-bar" />
    <van-popup
      v-model="addressShow"
      position="bottom"
      :style="{ height: '70%' }"
    >
      <!-- <van-button plain type="info" style="float:left;border: none;">取消</van-button>
      <van-button plain type="info" style="float:right;border: none;">确定</van-button> -->
      <!-- <van-area ref="myArea" :area-list="areaList" :columns-num="3" @change="onChange" @confirm="onConfirm" @cancel="onCancel" /> -->
      <div class="relative">
        <XyAddress @getCheckResult="getCheckResult" />
      </div>
    </van-popup>
  </div>
</template>

<script land="script" scoped>
import Backtop from '@/components/backtop/backtop.vue' // 返回顶部
// import baseScroll from '@/components/scroll/scroll.vue' // 导航栏
import BScroll from 'better-scroll'
import XyAddress from '@/components/address/address.vue'
import SearchBox from '@/components/search/search-box'
import { searchProduct } from '@/store/module.js'
import commonList from '@/components/list/commonList1.vue'
import { searchProductList, addComparision, cancelComparision, listComparisionDetail, getDefAddress } from '@/store/module.js'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'GoodsList',
  components: {
    // baseScroll,
    Backtop,
    SearchBox,
    XyAddress,
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      productIdList: [],
      produceList: [],
      stockStatus: [],
      xlSx: '',
      jgSx: '',
      sjsjSx: '',
      canClick: true,
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      sheng: '',
      shi: '',
      xian: '',
      xiang: '',
      addressShow: false,
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      state4: false,
      state1: false, // 筛选条件中分类条件是否已选择
      state2: false, // 筛选条件中品牌条件是否已选择
      state3: false, // 筛选条件中价格条件是否已选择
      loading: false,
      finished: false,
      showQhzs: false,
      active1: '',
      checked: [], // 对比商品id数组
      sxjxState: true, // 控制升序或降序
      active: 0,
      state: true, // 控制手动输入价格是否展示
      isActive1: '', // 控制筛选条件商品分类选中状态
      isActive2: '', // 控制筛选条件品牌选中状态
      isActive3: '', // 控制筛选条件价格选中状态
      searchList: [], // 搜索区域搜索条件
      selectGoodsDatas: {
        dsbs: this.$store.state.home.dsbs ? this.$store.state.home.dsbs : '',
        category_id: this.$store.state.home.categoryId ? this.$store.state.home.categoryId : '',
        price_now_min: '',
        price_now_max: '',
        search_input: '',
        searchKeyList: [],
        brand_name: '',
        ascFieldList: [],
        descFieldList: [],
        town_id: '',
        pageno: 1,
        pagerow: 30,
        stock: '',
        allowpayment: ''
      }, // 搜索条件对象
      goodsList: [], // 当前搜索到的所有商品列表
      number: '', // 当前展示商品数量
      totalNumber: '', // 当前搜索出的总数量
      searchInput: [],
      pages: 1,
      pagesize: 30,
      isLoading: '',
      price2: '',
      price1: '',
      itemList: [],
      nowIndex: 0,
      value1: '',
      value2: '',
      value3: '',
      option1: [
        { text: '销量最多', value: true },
        { text: '销量最少', value: false }
      ],
      option2: [
        { text: '价格最高', value: true },
        { text: '价格最低', value: false }
      ],
      option3: [
        { text: '上架时间最晚', value: true },
        { text: '上架时间最早', value: false }
      ],
      show: false,
      temp1: [],
      temp2: [],
      temp3: [],
      navs: [],
      address: {}
    }
  },
  watch: {
    swiperSendIndex(value) {
      // 接收 swiper组件传过来的索引，导航高亮
      this.nowIndex = value
      this.$refs.scrollContent.scrollToElement(this.$refs.tabitem[value], 0)
    },
    sjbqbt(value) {
      this.itemList = value

      // if (value.length > 0) {
      this.InitTabScroll()
      // }
    }
  },
  activated() {
    this.getlistComparisionDetail()
  },
  mounted() {
    this.$refs.searchBox.setQuery(this.selectGoodsDatas.search_input)

    // this.$nextTick(() => {
    //   // 初始化，保证刷新页面后内容区和导航键一致
    //   this.itemList = this.bt
    //   if (this.itemList.length !== 1) {
    //     this.InitTabScroll()
    //   }
    // })
    // 接收swiper组件发射的index进行导航按钮切换高亮和更新模板地址
    // this.$root.eventHub.$on('slideTab', this.slideTab)
  },
  created() {
    this.navs = this.$store.state.mall.navs
    if (this.$route.params.zq) {
      this.active1 = this.$route.params.zq
    }
    this.getlistComparisionDetail()
    this.$bus.$on('searchByInput', (searchInput) => {
      this.selectGoodsDatas.search_input = searchInput
      this.$bus.$emit('refList1', this.selectGoodsDatas)
    })
    this.selectGoodsDatas.dsbs = this.$route.params.zq
    this.selectGoodsDatas.category_id = this.$route.params.category_id
    this.selectGoodsDatas.search_input = this.$route.params.searchInput
    this.init()
    this.getSearchProduce()
    this.getDelAddress()
  },
  methods: {
    oninput(num) {
      var str = num
      var len1 = str.substr(0, 1)
      var len2 = str.substr(1, 1)
      // 如果第一位是0，第二位不是点，就用数字把点替换掉
      if (str.length > 1 && len1 === 0 && len2 !== '.') {
        str = str.substr(1, 1)
      }
      // 第一位不能是.
      if (len1 === '.') {
        str = ''
      }
      // 第一位不能是-
      if (len1 === '-') {
        str = ''
      }
      // 限制只能输入一个小数点
      if (str.indexOf('.') !== -1) {
        var str_ = str.substr(str.indexOf('.') + 1)
        if (str_.indexOf('.') !== -1) {
          str = str.substr(0, str.indexOf('.') + str_.indexOf('.') + 1)
        }
      }
      // 正则替换
      str = str.replace(/[^\d^\.]+/g, '') // 保留数字和小数点
      return str
    },
    getDelAddress() {
      this.asyncApiModule(getDefAddress).then(res => {
        if (res.province) {
          this.address.province = res.province
        }
        if (res.city) {
          this.address.city = res.city
        }
        if (res.county) {
          this.address.county = res.county
        }
        if (res.town && res.town !== '1') {
          this.address.town = res.town
        }
      }).catch(() => {
        this.loading = false
      })
    },
    changeClass1(index) {
      this.active = index
      if (index === 1) {
        if (this.xlSx === '') {
          this.xlSx = true
        } else {
          this.xlSx = !this.xlSx
        }
        this.changeClass(1, 'sale_count', this.xlSx)
      }
      if (index === 2) {
        if (this.jgSx === '') {
          this.jgSx = true
        } else {
          this.jgSx = !this.jgSx
        }
        this.changeClass(2, 'price_now', this.jgSx)
      }
      if (index === 3) {
        if (this.sjsjSx === '') {
          this.sjsjSx = true
        } else {
          this.sjsjSx = !this.sjsjSx
        }
        this.changeClass(3, 'update_time', this.sjsjSx)
      }
    },
    guanbi() {
      this.show = false
    },
    getPrizeLog(res) {
      this.state4 = true
      this.goodsList = res
      this.stockStatus = []
      this.produceList = []
      this.productIdList = []
      for (const good of this.goodsList) {
        this.stockStatus.push(true)
        this.productIdList.push(good.product_id)
        var produce = { skuId: good.product_id, num: 1, dsbs: good.dsbs }
        this.produceList.push(produce)
      }
      this.getStock()
    },
    init() {
      this.finished = false
      this.selectGoodsDatas.pageno = 1
      searchProductList.data = this.selectGoodsDatas
      this.list.postUrl = searchProductList
      this.list.reqData = this.selectGoodsDatas
      this.list.dataName = 'list'
      this.list.noDataText = '暂无更多订单'
    },
    getStock() {
      if (this.produceList.length > 0 && this.address) {
        var productSyncRequest = {
          province: this.address.province ? this.address.province : '',
          city: this.address.city ? this.address.city : '',
          county: this.address.county ? this.address.county : '',
          town: this.address.town ? this.address.town : '',
          produceList: this.produceList
        }
        this.$store.dispatch('goods/getStockList', productSyncRequest).then(res => {
          const result = res.result
          const resultArray = []
          this.stockStatus = []

          for (const good of this.goodsList) {
            if (good.product_id !== '') {
              this.stockStatus.push(true)
            }
          }
          for (let i = 0; i < result.length; i++) {
            resultArray.push(result[i].skuId)

            if (result[i].state === 34) {
              if (this.productIdList.indexOf(result[i].skuId) !== -1) {
                this.stockStatus.splice(this.productIdList.indexOf(result[i].skuId), 1, false)
              }
            }
          }
          let sfdqzq = false
          for (const good of this.goodsList) {
            if (resultArray.indexOf(good.product_id) !== -1) {
              sfdqzq = true
            }
          }
          if (sfdqzq) {
            for (const good of this.goodsList) {
              if (resultArray.indexOf(good.product_id) === -1 && this.productIdList.indexOf(good.product_id) !== -1) {
                this.stockStatus.splice(this.productIdList.indexOf(good.product_id), 1, false)
              }
            }
          }
        })
          .catch((error) => {
            console.log('error-------------------------------------------------------', error)
            this.loading = false
          })
      } else {
        for (let i = 0; i < this.stockStatus.length; i++) {
          this.stockStatus.splice(i, 1, true)
        }
      }
    },
    // 获取对比商品列表
    getlistComparisionDetail() {
      this.asyncApiModule(listComparisionDetail).then(res => {
        this.checked = []
        for (let i = 0; i < res.length; i++) {
          this.checked.push(res[i].id)
        }
      })
    },
    // 加入对比
    async addCompare(product_id, dsbs) {
      if (!product_id) {
        this.$toast('商品信息错误，加入对比失败')
        return
      }
      if (this.checked.indexOf(product_id) === -1) {
        if (this.checked.length >= 4) {
          this.$dialog.confirm({
            title: '提示',
            message: '对比栏已满，一次性最多可对比4件商品，是否前往删除已选对比商品?'
          }).then(() => {
            this.$router.push({
              name: 'GoodsCompare',
              params: {
                address: this.address
              }
            })
          }).catch(() => {
          })
        } else {
          this.checked.push(product_id)
          addComparision.data = { e_product_id: null, product_id: product_id }
          this.asyncApiModule(addComparision).then(res => {
            this.$toast('加入对比成功')
          })
            .catch(() => {
            })
        }
      } else {
        this.checked.splice(this.checked.indexOf(product_id), 1)
        cancelComparision.data = { e_product_id: '', product_id: product_id }
        this.asyncApiModule(cancelComparision).then(res => {
          this.$toast('取消对比成功')
        })
          .catch(() => {
          })
      }
    },
    // 获取地址组件返回值
    getCheckResult(sheng, shi, xian, xiang, state) {
      if (sheng) {
        this.sheng = sheng.text
      } else {
        this.sheng = ''
      }
      if (shi) {
        this.shi = shi.text
      } else {
        this.shi = ''
      }
      if (xian) {
        this.xian = xian.text
      } else {
        this.xian = ''
      }
      if (xiang) {
        this.xiang = xiang.text
      } else {
        this.xiang = ''
      }
      if (!state) {
        this.addressShow = state
      }
      this.selectGoodsDatas.ascFieldList = []
      this.selectGoodsDatas.descFieldList = []
      if (xiang) {
        this.selectGoodsDatas.town_id = xiang.value
      } else {
        this.selectGoodsDatas.town_id = ''
      }
      this.$bus.$emit('refList1', this.selectGoodsDatas)
    },
    changeClass(index, val, value) {
      this.active = index
      if (index === 0) {
        this.addressShow = true
        return
      }
      this.state4 = false
      this.sxjxState = value
      if (this.sxjxState === false) {
        this.selectGoodsDatas.ascFieldList = []
        this.selectGoodsDatas.descFieldList = []
        this.selectGoodsDatas.descFieldList.push(val)
        this.$bus.$emit('refList1', this.selectGoodsDatas)
      } else if (this.sxjxState === true) {
        this.selectGoodsDatas.descFieldList = []
        this.selectGoodsDatas.ascFieldList = []
        this.selectGoodsDatas.ascFieldList.push(val)
        this.$bus.$emit('refList1', this.selectGoodsDatas)
      }
    },
    onRefresh() {
      this.isLoading = true
      this.$bus.$emit('refList1', this.selectGoodsDatas)
    },
    goReset() {
      this.canClick = true
      this.state1 = false
      this.state2 = false
      this.state3 = false
      this.price1 = ''
      this.price2 = ''
      this.selectGoodsDatas.search_input = ''
      this.selectGoodsDatas.brand_name = ''
      this.selectGoodsDatas.price_now_min = ''
      this.selectGoodsDatas.price_now_max = ''
      this.selectGoodsDatas.category_id = ''
      this.getSearchProduce()
      this.$bus.$emit('refList1', this.selectGoodsDatas)
      this.state = true
      this.isActive1 = ''
      this.isActive2 = ''
      this.isActive3 = ''
    },
    goSearch1() {
      if (this.price1 && this.price2) {
        if (parseFloat(this.price2) < parseFloat(this.price1)) {
          this.$toast('最高价不能小于最低价')
          return
        }
      }
      this.selectGoodsDatas.price_now_min = this.price1
      this.selectGoodsDatas.price_now_max = this.price2
      this.$bus.$emit('refList1', this.selectGoodsDatas)
      this.show = false
      window.scrollTo(0, 0)
    },
    clickCategory(item, value, index) {
      this.canClick = false
      this.selectGoodsDatas.category_id = value
      this.isActive1 = 0
      this.getSearchProduce(1, item)
    },
    clickBrandName(item, name, index) {
      this.canClick = false
      this.selectGoodsDatas.brand_name = name
      this.isActive2 = 0
      this.getSearchProduce(2, item)
    },
    clickPrice(item, value, index) {
      this.canClick = false
      this.price1 = value.slice(0, value.indexOf('-'))
      this.price2 = value.slice(value.indexOf('-') + 1, value.length)
      this.isActive3 = 0
      this.getSearchProduce(3, item)
    },
    chongzhi(val) {
      this.canClick = false
      if (val === 1) {
        this.isActive1 = ''
        this.state1 = false
        this.selectGoodsDatas.category_id = ''
        this.getSearchProduce()
      }
      if (val === 2) {
        this.isActive2 = ''
        this.state2 = false
        this.selectGoodsDatas.brand_name = ''
        this.getSearchProduce()
      }
      if (val === 3) {
        this.isActive3 = ''
        this.state3 = false
        this.state = true
        this.selectGoodsDatas.price_now_min = ''
        this.selectGoodsDatas.price_now_max = ''
        this.price1 = ''
        this.price2 = ''
        this.getSearchProduce()
      }
    },
    // 获取搜索区域搜索条件
    getSearchProduce(val, item) {
      const pra = {
        'dsbs': this.selectGoodsDatas.dsbs,
        'search_input': this.selectGoodsDatas.search_input,
        'category_id': this.selectGoodsDatas.category_id,
        'searchKeyList': this.selectGoodsDatas.searchKeyList,
        'brand_name': this.selectGoodsDatas.brand_name,
        'price_now_min': this.selectGoodsDatas.price_now_min,
        'price_now_max': this.selectGoodsDatas.price_now_max
      }
      searchProduct.data = pra
      this.asyncApiModule(searchProduct).then(res => {
        this.searchList = res
        if (val === 1) {
          this.isActive1 = 0
          this.searchList.categorySearchList = []
          this.searchList.categorySearchList.push(item)
          this.temp1 = this.searchList.categorySearchList
          this.state1 = true
        }
        if (val === 2) {
          this.isActive2 = 0
          this.searchList.brandList = []
          this.searchList.brandList.push(item)
          this.temp2 = this.searchList.brandList
          this.state2 = true
        }
        if (val === 3) {
          this.isActive3 = 0
          this.searchList.priceList = []
          this.searchList.priceList.push(item)
          this.state = false
          this.temp3 = this.searchList.priceList
          this.state3 = true
        }
        if (this.state1) {
          this.searchList.categorySearchList = this.temp1
        }
        if (this.state2) {
          this.searchList.brandList = this.temp2
        }
        if (this.state3) {
          this.searchList.priceList = this.temp3
        }
        // if (val === 4) {
        //   if (this.state2) {
        //     this.searchList.brandList = this.temp2
        //   }
        //   if (this.state3) {
        //     this.searchList.priceList = this.temp3
        //   }
        // }
        // if (val === 5) {
        //   if (this.state1) {
        //     this.searchList.categorySearchList = this.temp1
        //   }
        //   if (this.state3) {
        //     this.searchList.priceList = this.temp3
        //   }
        // }
        // if (val === 6) {
        //   if (this.state2) {
        //     this.searchList.brandList = this.temp2
        //   }
        //   if (this.state1) {
        //     this.searchList.categorySearchList = this.temp1
        //   }
        // }
        this.canClick = true
      })
    },
    goSearch(val) {
      this.$router.push({
        name: 'Search'
      })
    },
    searchByZq(dsbs, index) {
      this.active1 = dsbs
      // this.selectGoodsDatas = {}
      this.selectGoodsDatas.dsbs = dsbs
      this.getSearchProduce()
      this.$bus.$emit('refList1', this.selectGoodsDatas)
    },
    showPopup() {
      this.show = true
    },
    showQhzsBtn() {
      this.showQhzs = !this.showQhzs
    },
    itemClick(index) {
      this.nowIndex = index

      this.$emit('changeTab', index)
    },
    InitTabScroll() {
      let width = 0
      if (this.itemList.length === 1) {
        width = 120
      } else {
        width = 120 * this.itemList.length
        // for (let i = 0; i < this.itemList.length; i++) {
        //   width += 200 //getBoundingClientRect() 返回元素的大小及其相对于视口的位置
        // }
        this.$refs.tabWrapper.style.width = width + 'px'
      }

      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.tab, {
            startX: 0,
            click: true,
            scrollX: true,
            scrollY: false,
            eventPassthrough: 'vertical'
          })
        } else {
          this.scroll.refresh()
        }
      })
    },
    handleSubmit() {
      this.$refs.scrollContent.scrollToElement(this.$refs.tabitem[3], 0)
    },
    goGoodsdetail(id, spmc) {
      this.$store.commit('goods/SET_SPMC', spmc)
      this.$store.commit('goods/SET_SPID', id)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    goBack() {
      this.$router.push('MallHome')
    },
    goGoodsCompare() {
      if (this.checked.length === 0) {
        this.$toast('尚未选择对比商品，请先选择！')
        return
      }
      this.$router.push({
        name: 'GoodsCompare',
        params: {
          address: this.address
        }
      })
    }

  }
}
</script>

<style lang="css" scoped>
.van-sticky--fixed .nav-bar{box-shadow: none!important;}
</style>
